.footer {
    background: #00843e;
    color: #ffffff;
    min-height: 100px;
    padding: 30px 0;
    flex: 0 0 auto;
}

.footerRow {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.footerRowItem {
    color: #ffffff;
}

.footerHr {
    margin-top: 40px;
    margin-bottom: 10px;
    border-color: #ffffff;
}

@media screen and (max-width: 767px) {
    .footerRow {
        flex-direction: column;
    }
    .footerRowItem:not(:last-child) {
        margin-bottom: 30px;
    }
}

.footerRowTitle {
    font-weight: bold;
}

.footerRowText {
    color: #ffffff;
    display: block;
}

.footerRowText:not(:last-child) {
    margin-bottom: 10px;
}

.footerRowItem:hover, .footerRowLink:hover, .footerRowText:hover {
    color: #ffffff;
}

.footerRowCenter {
    margin-left: 7%;
}

.footerRowRight {
    margin-right: 5%;
}

@media screen and (max-width: 1023px) {
    .footerRowCenter {
        margin-left: 10%;
    }
    .footerRowRight {
        margin-right: 7%;
    }
}

@media screen and (max-width: 767px) {
    .footerRowCenter {
        margin-left: 0;
    }
    .footerRowRight {
        margin-right: 0;
    }
}

.footerRowYear {
    margin-top: 10px;
}